.header {
    /*  border: 1px solid black;*/
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 5px;
    font-size: 25px;
    font-weight: 200;
    margin-top: 20px;
}

h2 {

}

.line{
    height: 3px;
    width: 60px;
    margin: 30px 0;
    border-radius: 3px;

}

.light.header{
    color: #404040;
}

.light .line{
    background-color: #010051;
}

.dark .line{
    background-color: #ff7fc0;
}

.dark.header{
    color: #DEABD4;
}
