.skills {
    min-height: 100vh;
    background-color: #ffe2ea;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
  /*  border: 1px solid black;*/
    width: 80%;
    display: flex;
    align-items: center;
    text-align: center;
    flex-direction: column;
}


.photo img {
    width: 150px;
    height: 150px;
    background-color: #696969;
}
.photo{
    padding: 15px;
}

.blockSkills {
 /*   border: 1px solid black;*/
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    min-height: 90%;
}

.skill {
/*    border: 1px solid black;*/
    width: 300px;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: #404040;
    margin: 30px;
    background-color: #eed2d9;
}

.description{
    padding: 15px;
}

@media screen and (max-width: 700px ) {
    .blockSkills{
       flex-direction: column;

    }

}

@media screen and (max-width: 1280px ) {
    .blockSkills{
        justify-content: space-evenly;
    }
}

@media screen and (max-width: 450px ) {
    .skill{
        width: 80%;
    }
    .photo img {
        height: 120px;
        width: 120px;
    }

}